<template>
  <div class="mod-config">
    <div ref="testamortizedataFrom" style="margin-bottom: 20px;">
      <el-card class="filter-container" shadow="never">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button
            style="float:right"
            type="primary"
            @click="getDataList()"
            size="small">
            <icon-svg name="chaxun" class="icon"></icon-svg>查询搜索</el-button>
          <el-button v-if="isAuth('testamortizedata:add')"  size="small" style="float:right;margin-right: 10px" type="primary" @click="addOrUpdateHandle()"><icon-svg name="addNew" class="icon"></icon-svg>  新增</el-button>
          <el-button v-if="isAuth('testamortizedata:add')" type="primary"size="small" style="float:right;margin-right: 5px" @click="uploadExcel()"><icon-svg name="yunduanshangchuan" class="icon"></icon-svg>  导入</el-button>
          <el-button v-if="isAuth('testamortizedata:add')" type="primary"size="small" style="float:right;margin-right: 5px":disabled="this.downloadDisable" @click="exportExcel()"><icon-svg name="yunduanxiazai" class="icon"></icon-svg>  导出</el-button>
          <el-button v-if="isAuth('testamortizedata:delete')" type="danger" size="small" style="float:right;margin-right: 10px" @click="deleteHandle()" :disabled="dataListSelections.length <= 0"><icon-svg name="shanchu1" class="icon"></icon-svg> 批量删除</el-button>
          <el-button
            style="float:right;"
            @click="handleResetSearch()"
            size="small">重置
          </el-button>
        </div>
        <div style="margin-top: 15px">
          <el-form :inline="true" :model="dataForm"  @keyup.enter.native="getDataList()">
            <el-row>
              <el-col :span="6">
                <el-form-item label="到期时间">
                <el-input v-model="dataForm.column10" placeholder="截止2017.12已确认利息"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="到期时间">
                <el-input v-model="dataForm.column11" placeholder="2018年已确认利息"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="6">
                <el-form-item label="到期时间">
                  <el-date-picker
                    v-model="dataForm.column27"
                    type="date"
                    value-format="yyyy-MM-dd"
                    placeholder="到期时间">
                  </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="展期到期日">
                  <el-date-picker
                    v-model="dataForm.column28"
                    type="date"
                    value-format="yyyy-MM-dd"
                    placeholder="展期到期日">
                  </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="计息日">
                  <el-date-picker
                    v-model="dataForm.column29"
                    type="date"
                    value-format="yyyy-MM-dd"
                    placeholder="计息日">
                  </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="转让清单编号">
                  <el-input v-model="dataForm.column3" placeholder="转让清单编号"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
      </el-card>
    </div>
    <el-table
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      :data="dataList"
      border
      stripe
      :height="tableHight"
      ref="testamortizedataTable"
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      @row-click="toggleSelection"
      style="width: 100%;">
      <el-table-column
        type="selection"
        header-align="center"
        align="center"
        width="50">
      </el-table-column>
      <el-table-column
        prop="id"
        header-align="center"
        align="center"
        label="主键id">
      </el-table-column>
      <el-table-column
        prop="column2"
        header-align="center"
        align="center"
        label="序号">
      </el-table-column>
      <el-table-column
        prop="column3"
        header-align="center"
        align="center"
        label="转让清单编号">
      </el-table-column>
      <el-table-column
        prop="column4"
        header-align="center"
        align="center"
        label="截止2017.12已确认服务费">
      </el-table-column>
      <el-table-column
        prop="column5"
        header-align="center"
        align="center"
        label="2018已确认服务费">
      </el-table-column>
      <el-table-column
        prop="column6"
        header-align="center"
        align="center"
        label="2019.1确认金额">
      </el-table-column>
      <el-table-column
        prop="column7"
        header-align="center"
        align="center"
        label="2019.2确认金额">
      </el-table-column>
      <el-table-column
        prop="column8"
        header-align="center"
        align="center"
        label="2019.3确认金额">
      </el-table-column>
      <el-table-column
        prop="column9"
        header-align="center"
        align="center"
        label="交易合同名称">
      </el-table-column>
      <el-table-column
        prop="column10"
        header-align="center"
        align="center"
        label=" 截止2017.12已确认利息">
      </el-table-column>
      <el-table-column
        prop="column11"
        header-align="center"
        align="center"
        label=" 2018年已确认利息">
      </el-table-column>
      <el-table-column
        prop="column12"
        header-align="center"
        align="center"
        label=" 2019.1确认利息金额">
      </el-table-column>
      <el-table-column
        prop="column13"
        header-align="center"
        align="center"
        label="2019.2确认利息金额">
      </el-table-column>
      <el-table-column
        prop="column14"
        header-align="center"
        align="center"
        label=" 2019.3确认利息金额">
      </el-table-column>
      <el-table-column
        fixed="right"
        header-align="center"
        align="center"
        width="150"
        label="操作">
        <template slot-scope="scope">
          <el-button type="text" size="small" v-on:click.stop="addOrUpdateHandle(scope.row.id)"> <icon-svg name="bianjisekuai" class="icon"></icon-svg>修改</el-button>
          <el-button type="text" size="small"  class="red" v-on:click.stop="deleteHandle(scope.row.id)"><icon-svg name="shanchu" class="icon"></icon-svg>删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChangeHandle"
      @current-change="currentChangeHandle"
      :current-page="pageIndex"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      :total="totalPage"
      layout="total, sizes, prev, pager, next, jumper">
    </el-pagination>
    <!-- 弹窗, 新增 / 修改 -->
    <add-or-update v-if="addOrUpdateVisible" ref="addOrUpdate" @refreshDataList="getDataList"></add-or-update>
      <!--弹窗上传-->
      <upload v-if="uploadVisible" ref="upload"></upload>
  </div>
</template>
<script>
  import AddOrUpdate from './testamortizedata-add-update'
  import Upload from './testamortizedata-upload'
  export default {
    data () {
      return {
        dataForm: {
          key: ''
        },
        dataList: [],
        pageIndex: 1,
        pageSize: 20,
        totalPage: 0,
        dataListLoading: false,
        dataListSelections: [],
        addOrUpdateVisible: false,
        uploadVisible: false,
        downloadDisable: false,
        offsetHeight: 0,
        tableHight: 0
      }
    },
    components: {
      AddOrUpdate,
      Upload
    },
    computed: {
      documentClientHeight: {
        get () { return this.$store.state.common.documentClientHeight }
      }
    },
    watch: {
      documentClientHeight: function (val) {
        this.tableHight = val - this.offsetHeight - 189
      }
    },
    activated () {
      this.getDataList()
    },
    mounted () {
      this.offsetHeight = this.$refs.testamortizedataFrom.offsetHeight
    },
    methods: {
      handleResetSearch () {
        this.dataForm = Object.assign({})
      },
      // 获取数据列表
      getDataList () {
        this.dataListLoading = true
        this.$http({
          url: this.$http.adornUrl('/testamortizedata/list'),
          method: 'get',
          params: this.$http.adornParams({
            'page': this.pageIndex,
            'limit': this.pageSize,
            'key': this.dataForm.key
          })
        }).then(({data}) => {
          if (data && data.code === 0) {
            this.dataList = data.page.list
            this.totalPage = data.page.totalCount
          } else {
            this.dataList = []
            this.totalPage = 0
          }
          this.tableHight = this.$store.state.common.documentClientHeight - this.offsetHeight - 189
          this.dataListLoading = false
        })
      },
      // 每页数
      sizeChangeHandle (val) {
        this.pageSize = val
        this.pageIndex = 1
        this.getDataList()
      },
      // 当前页
      currentChangeHandle (val) {
        this.pageIndex = val
        this.getDataList()
      },
      // 多选
      selectionChangeHandle (val) {
        this.dataListSelections = val
      },
      // 触发一行的
      toggleSelection (row) {
        if (row) {
          this.$refs.testamortizedataTable.toggleRowSelection(row)
        } else {
          this.$refs.testamortizedataTable.clearSelection()
        }
      },
      // 新增 / 修改
      addOrUpdateHandle (id) {
        this.addOrUpdateVisible = true
        this.$nextTick(() => {
          this.$refs.addOrUpdate.init(id)
        })
      },
      uploadExcel (id) {
        this.uploadVisible = true
        this.$nextTick(() => {
          this.$refs.upload.init()
        })
      },
      exportExcel () {
        this.$notify({
          title: '警告',
          message: '开始下载，请勿多次点击',
          type: 'warning'
        })
        this.$axios({
          url: this.$http.adornUrl(`/testamortizedata/export?token=${this.$cookie.get('token')}`),
          method: 'GET',
          responseType: 'blob',
          params: {
            id: '',
            column2: '',
            column3: '',
            column4: '',
            column5: '',
            column6: '',
            column7: '',
            column8: '',
            column9: '',
            column10: '',
            column11: '',
            column12: '',
            column13: '',
            column14: ''
          }
        }).then((response) => {
          const blob = new Blob([response.data], {type: response.data.type})
          const url = window.URL.createObjectURL(blob)
          const link = document.createElement('a')
          link.href = url
          let name = decodeURI(response.headers['name'])
          let fileName = 'unknown'
          if (name) {
            fileName = decodeURI(name)
          }
          link.setAttribute('download', fileName)
          document.body.appendChild(link)
          link.click()
          link.remove()
          window.URL.revokeObjectURL(url)
        })
      },
      // 删除
      deleteHandle (id) {
        var ids = id ? [id] : this.dataListSelections.map(item => {
          return item.id
        })
        this.$confirm(`确定对[id=${ids.join(',')}]进行[${id ? '删除' : '批量删除'}]操作?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl('/testamortizedata/delete'),
            method: 'post',
            data: this.$http.adornData(ids, false)
          }).then(({data}) => {
            if (data && data.code === 0) {
              this.$message({
                message: '操作成功',
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.getDataList()
                }
              })
            } else {
              this.$message.error(data.msg)
            }
          })
        })
      }
    }
  }
</script>
<style scoped>
    .red{
        color: #ff0000;
    }
</style>



