<template>
  <el-dialog
    :title="!dataForm.id ? '新增' : '修改'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    center>
    <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="80px">
        <el-row>
            <el-col :span="12">
                <el-form-item label="主键id" prop="id">
                    <el-input v-model="dataForm.id" placeholder="主键id"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="序号" prop="column2">
                    <el-input v-model="dataForm.column2" placeholder="序号"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="转让清单编号" prop="column3">
                    <el-input v-model="dataForm.column3" placeholder="转让清单编号"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="截止2017.12已确认服务费" prop="column4">
                    <el-input v-model="dataForm.column4" placeholder="截止2017.12已确认服务费"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="2018已确认服务费" prop="column5">
                    <el-input v-model="dataForm.column5" placeholder="2018已确认服务费"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="2019.1确认金额" prop="column6">
                    <el-input v-model="dataForm.column6" placeholder="2019.1确认金额"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="2019.2确认金额" prop="column7">
                    <el-input v-model="dataForm.column7" placeholder="2019.2确认金额"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label="2019.3确认金额" prop="column8">
                    <el-input v-model="dataForm.column8" placeholder="2019.3确认金额"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="交易合同名称" prop="column9">
                    <el-input v-model="dataForm.column9" placeholder="交易合同名称"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label=" 截止2017.12已确认利息" prop="column10">
                    <el-input v-model="dataForm.column10" placeholder=" 截止2017.12已确认利息"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label=" 2018年已确认利息" prop="column11">
                    <el-input v-model="dataForm.column11" placeholder=" 2018年已确认利息"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label=" 2019.1确认利息金额" prop="column12">
                    <el-input v-model="dataForm.column12" placeholder=" 2019.1确认利息金额"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="2019.2确认利息金额" prop="column13">
                    <el-input v-model="dataForm.column13" placeholder="2019.2确认利息金额"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="12">
                <el-form-item label=" 2019.3确认利息金额" prop="column14">
                    <el-input v-model="dataForm.column14" placeholder=" 2019.3确认利息金额"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        dataForm: {
            id: '',
            column2: '',
            column3: '',
            column4: '',
            column5: '',
            column6: '',
            column7: '',
            column8: '',
            column9: '',
            column10: '',
            column11: '',
            column12: '',
            column13: '',
            column14: ''
        },
        dataRule: {
          column2: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column3: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column4: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column5: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column6: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column7: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column8: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column9: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column10: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column11: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column12: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column13: [{ required: true, message: '不能为空', trigger: 'blur' }],
          column14: [{ required: true, message: '不能为空', trigger: 'blur' }]        }
      }
    },
    methods: {
      init (id) {
        this.dataForm.id = id || ''
        this.visible = true
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if (this.dataForm.id) {
            this.$http.get( this.$http.adornUrl(`/testamortizedata/info`), {params: this.$http.adornParams({id: this.dataForm.id})}).then(({data}) => {
              if (data && data.code === 0) {
                for (let key in data.testamortizedata) {
                  if (data.testamortizedata[key]) {
                    this.dataForm[key] = data.testamortizedata[key]
                  }
                }
              }
            })
          }
        })
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.$http({
              url: this.$http.adornUrl(`/testamortizedata/${!this.dataForm.id ? 'add' : 'update'}`),
              method: 'post',
              data: this.$http.adornData({
                'id': this.dataForm.id || undefined,
                'column2': this.dataForm.column2,
                'column3': this.dataForm.column3,
                'column4': this.dataForm.column4,
                'column5': this.dataForm.column5,
                'column6': this.dataForm.column6,
                'column7': this.dataForm.column7,
                'column8': this.dataForm.column8,
                'column9': this.dataForm.column9,
                'column10': this.dataForm.column10,
                'column11': this.dataForm.column11,
                'column12': this.dataForm.column12,
                'column13': this.dataForm.column13,
                'column14': this.dataForm.column14
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              } else {
                this.$message.error(data.msg)
              }
            })
          }
        })
      }
    }
  }
</script>



